.page-channel {
  display: flex;
  flex-direction: column;
}
.page-channel .header {
  position: relative;
  line-height: 50px;
  text-align: center;
  font-weight: bolder;
  color: #fff;
  background-color: #d43d3d;
}
.page-channel .header .back {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 44px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 22px;
}
.page-channel .channel-title {
  font-size: 12px;
  padding: 10px;
  color: #666;
}
.page-channel .channel-list {
  padding: 0 10px;
}
.page-channel .channel-list .item {
  display: inline-block;
  width: 25%;
  margin-bottom: 10px;
  -webkit-animation: zoomIn 0.3s ease;
          animation: zoomIn 0.3s ease;
}
.page-channel .channel-list .item .channel {
  font-size: 16px;
  display: block;
  margin: 0 8px;
  border: 1px solid #ccc;
  text-align: center;
  line-height: 2em;
}
.page-channel .channel-list .item .channel.disabled {
  background-color: #f0f0f0;
}
@-webkit-keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
  }
}
@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
  }
}
